define("oozie-designer/helpers/date-helper", ["exports", "ember"], function (exports, _ember) {
  exports.dateHelper = dateHelper;

  function dateHelper(dateStr) {
    var date = dateStr && new Date(dateStr);
    if (!date || isNaN(date.getTime())) {
      return "";
    }
    return [moment(date).format("MM/DD/YYYY hh:mm A")].join("");
  }

  exports["default"] = _ember["default"].Helper.helper(dateHelper);
});
